@charset "UTF-8";
.section1 {
  box-sizing: border-box; }
  @media screen and (min-width: 768px) {
    .section1 {
      background-image: url("./../../images/firs-screen.jpg");
      width: 100%;
      height: 740px;
      background-position: right; } }
  @media screen and (min-width: 0) and (max-width: 767px) {
    .section1 {
      background-image: url("./../../images/first screen img phone.jpg");
      height: calc(100vh - 50px);
      width: 100%;
      background-position: 0 0;
      background-repeat: no-repeat; } }
  .section1__content {
    display: inline-block;
    position: relative;
    top: 22.83784%;
    left: 18.75%; }
    @media screen and (min-width: 1200px) {
      .section1__content {
        top: 22.83784%;
        left: 18.75%; } }
    @media screen and (min-width: 768px) and (max-width: 1199px) {
      .section1__content {
        top: 22.83784%;
        left: 40px; } }
    @media screen and (min-width: 0) and (max-width: 767px) {
      .section1__content {
        position: static;
        padding-left: 8.88889%;
        padding-right: 8.88889%;
        text-align: center; } }
  .section1__slogan {
    /* Экономьте время и деньги на поиск лучшего жилья в новостройке */
    font-weight: bold;
    font-size: 2.85714rem;
    line-height: 1.35em;
    width: 602px;
    margin-bottom: 2.21429rem; }
    @media screen and (min-width: 0) and (max-width: 767px) {
      .section1__slogan {
        display: flex;
        width: 100%;
        font-size: 1.85714rem;
        line-height: 1.34615em;
        margin-top: calc(50vh - 151px); } }
  .section1__description {
    /* Выбирайте из 13 164 квартир в 123 ЖК Новосибирска */
    font-style: normal;
    font-weight: 500;
    font-size: 1.42857rem;
    line-height: 1.35em;
    margin-bottom: 2.35714rem; }
  .section1__button {
    width: 260px; }
    @media screen and (min-width: 0) and (max-width: 767px) {
      .section1__button {
        margin: auto; } }
